.we-do {
    width: 75%;
    margin: auto;
    margin-top: 4rem;
    background-color: #000;
    font-weight: 550;
    font-size: 48px;
}

.gradient {
    text-align: center;
    color: rgb(218, 218, 218);
    /* background: linear-gradient(137deg, rgb(99, 98, 99) 0%, rgb(200, 200, 200) 79%, rgb(220, 220, 220) 82%, rgb(200, 200, 200) 85%, rgb(91, 90, 90) 100%); */
    /* color:transparent; */
    /* -webkit-background-clip: text; */
    /* background-clip: text; */

}

.mark {
    height: fit-content;
}

.mark li {
    display: inline;
    padding: 8px;
}

.mark span {
    color: rgb(17, 110, 187);
    padding: 14px;
}

#we-do-title {
        margin: auto;
        margin-top: 5rem;
        width: 40%; 
        text-align: center;
        font-size: 58px;
        color: rgb(23, 130, 218);
        font-weight: 700;
        border-bottom: solid 2px rgb(23, 130, 218);
        text-transform: uppercase;
        font-family: "Oswald", sans-serif;
        text-rendering: optimizeLegibility;
}

@media screen and (min-width: 200px) and (max-width: 500px) {
    .we-do {
        width: 100%;
        height: 50vh;
        font-size: 34px;
        text-align: left;
    }

    .mark li {
        display: flex;
        text-align: left;
    }

    .mark span {
        color: rgb(17, 110, 187);
        padding: 4px 16px 0 0;
    }

    #we-do-title {
        margin: auto;
        margin-top: 5rem;
        width: 80%; 
        text-align: center;
        font-size: 42px;
