.form {
    background-color: #000;
    margin: auto;
    font-family: 'Oswald', sans-serif;
    width: fit-content; 
    padding: 24px;
    height: fit-content;
    border-radius: 3px;
    text-transform: uppercase;
    border: solid 1px #333;
    margin-top: 0;
    padding-bottom: 2rem;
}

#head {
    color: #fff;
    padding-left: 16px;
    font-weight: 700;
    font-size: 20px;
}

#input {
    display: block;
    outline: none;
    margin: 16px;
    padding: 12px;
    border-radius: 3px;
    border: 0;
    width: 315px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff; 
    
}

#input:focus {
    background-color: rgb(214, 205, 205);
}

#select {
    display: block;
    outline: none;
    margin: 16px;
    padding: 12px;
    border-radius: 3px;
    border: 0;
    width: 340px;
    background-color: #fff; 
}

#submit {
    margin: 0 1rem;
    width: 340px;
    padding: 12px;
    background-color: rgb(31, 114, 209);
    color: #fff;
    border: none;
    border-radius: 3px;
}

@media screen and (min-width: 200px) and (max-width: 800px) {
    .form {
        border: none;
        margin-top: 0;
    }

    #input {
        margin: 16px;
        padding: 12px;
        width: 250px;
    }

    #select {
        margin: 16px;
        padding: 12px;
        border-radius: 3px;
        border: 0;
        width: 276px;
        background-color: #fff; 
    }

    #submit {
        padding: 12px;
        width: 277px;
    }
}


@media screen and (min-width: 801px) and (max-width: 1000px) {
    .form {
         width: 230px;
    }
 
    #input {
        margin: 16px;
        padding: 6px;
        width: 190px;
    }

    #select {
        margin: 16px;
        padding: 6px;
        border-radius: 3px;
        border: 0;
        width: 203px;
        background-color: #fff; 
    }

    #submit {
        padding: 8px;
        width: 205px;
    }

 }


 @media screen and (min-width: 1001px) and (max-width: 1300px) {
    .form {
         width: 310px;
    }
 
    #input {
        margin: 16px;
        padding: 8px;
        width: 270px;
    }

    #select {
        margin: 16px;
        padding: 8px;
        border-radius: 3px;
        border: 0;
        width: 287px;
        background-color: #fff; 
    }

    #submit {
        padding: 8px;
        width: 288px;
    }

 }