#plan-box {
    width: 100%;
    background-color: #fff;
    text-align: center;
    height: fit-content;
}

.plans {
    background-image: url('../../assets/dots.svg');
    display: grid;
    grid-template-columns: auto auto auto auto;
    justify-content: center;
    column-gap: 16px;
    width: 100%;
    margin: auto;
    height: fit-content;
    color: #222;
    padding: 4rem 0 8rem 0;
    background-color: #fff;
}

#individual-section {
    font-size: 28px;
    background-color: rgb(255, 255, 255);
    text-align: left;
    /* border: 1px solid #aeaeae; */
    border-radius: 3px;
    box-shadow: 12px 12px 12px 0px rgba(0, 0, 0, 0.2), 0px 6px 20px 12px rgba(0, 0, 0, 0.19);
    width: 225px;
    margin: 0 1rem 0 1rem;
    padding-top: 16px;
}

/* #head {
    background-color: #aeaeae;
    height: 1px;
    width: 80%;
    border: none;
} */

#price {
    display: block;
    font-weight: 700;
    padding: 16px 24px 8px 24px;
    font-size: 38px;
    text-rendering: optimizeLegibility;
}

#services {
    font-size: 16px;
    text-align: left;
    list-style-image: url('../../assets/check.svg');
    padding: 12px;
}

#services li {
    margin-left: 2rem;
    padding: 6px;
}

#type {
    font-size: 22px;
    margin: 0;
    text-align: left;
    padding: 24px;
    font-weight: 600;
    font-style: normal;
    font-weight: bolder;
    letter-spacing: .05em;
    line-height: normal;
    text-transform: uppercase;
    text-decoration: underline;
    font-family: 'Oswald', sans-serif;
    text-rendering: optimizeLegibility;
    color: #000;
}

#tab-switch {
    display: block;
    list-style-type: none;
    padding: 2rem 0 1rem 0;
    height: 60px;
    width: 55%;
    margin: auto;
}

#tab-switch ul {
    margin: 0;
    padding: 0;
    padding-bottom: 2rem;
    padding-top: 0.5rem;
    border-bottom: 1px solid #000;
}

#tab-switch li {
    font-size: 1.15em;
    font-style: normal;
    font-weight: bold;
    letter-spacing: .05em;
    line-height: normal;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    text-rendering: optimizeLegibility;
    display: inline;
    padding: 8px 12px 8px 12px;
    cursor: pointer;
    border: 1px solid #a8a6a6;
    border-radius: 3px;
    color: rgb(11, 136, 238);
  }

  #tab-switch li:hover {
    color: rgb(19, 124, 209); 
    border-color: #555; 
  }
  
  #note {
    font-size: 16px;
    font-weight: 600;
    font-style: normal;
    font-weight: bolder;
    letter-spacing: .05em;
    line-height: normal;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    text-rendering: optimizeLegibility;
    color: #000;
}

@media screen and (min-width: 200px) and (max-width: 500px) {
    #tab-switch {
        width: 85%;
        font-size: 12px;
    }

    #note {
        font-size: 12px;
    }

    #individual-section {
        margin-top: 16px;
    }

    .plans {
        width: 93%;
        overflow-y: scroll;
        scrollbar-width: thin;
        -webkit-overflow-scrolling: touch;
        mask-image: -webkit-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        mask-image: -moz-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        mask-image: linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: -webkit-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: -moz-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: linear-gradient(to right, transparent, white 10px, white 90%, transparent);
    }
 }

 @media screen and (min-width: 501px) and (max-width: 800px) {
    #tab-switch {
        width: 80%;
        font-size: 13px;
    }

    #note {
        font-size: 13px;
    }

    #individual-section {
        margin-top: 16px;
    }

    .plans {
        width: 95%;
        overflow-y: scroll;
        scrollbar-width: thin;
        -webkit-overflow-scrolling: touch;
        mask-image: -webkit-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        mask-image: -moz-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        mask-image: linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: -webkit-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: -moz-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: linear-gradient(to right, transparent, white 10px, white 90%, transparent);
    }
 }

 @media screen and (min-width: 801px) and (max-width: 1200px) {
    #tab-switch {
        width: 75%;
    }

    .plans {
        width: 97%;
        overflow-y: scroll;
        scrollbar-width: thin;
        -webkit-overflow-scrolling: touch;
        mask-image: -webkit-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        mask-image: -moz-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        mask-image: linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: -webkit-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: -moz-linear-gradient(to right, transparent, white 10px, white 90%, transparent);
        -webkit-mask-image: linear-gradient(to right, transparent, white 10px, white 90%, transparent);
    }
 }